.z_tip {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0;
  top: 0;
}

.z_tip .mask {
  width: 300px;
  height: 200px;
  position: absolute;
  left: 50%;
  margin-left: -150px;
  top: 50%;
  margin-top: -100px;
  background: #fff;
  border-radius: 10px;
  padding: 30px;
  box-sizing: border-box;
}

.z_tip .mask .btn {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
