.z_classmenagement {
  width: 100%;
  height: 100%;
  padding: 15px;
  box-sizing: border-box;
}

.z_classmenagement .classify {
  background: #fff;
  padding: 10px;
  box-sizing: border-box;
}

.z_classmenagement .classify p span {
  margin-right: 30px;
  line-height: 40px;
}

.z_classmenagement .classify .ipt {
  margin-top: 20px;
}

.z_classmenagement .classify .ipt span {
  margin-right: 10px;
  margin-left: 10px;
}

.z_classmenagement .tab {
  margin-top: 20px;
}

.z_classmenagement .active {
  background: blue;
  padding: 2px 10px;
  border-radius: 6px;
  color: #fff;
}
