.z_testdetail {
  width: 100%;
  height: 100%;
}

.z_testdetail .main {
  display: flex;
  justify-content: space-between;
}

.z_testdetail .main .m_left {
  width: 65%;
  background: #fff;
  border-radius: 5px;
  padding: 5px;
  margin-right: 6px;
}

.z_testdetail .main .m_left .item > div {
  background: #f7f9fa;
}

.z_testdetail .main .m_right {
  flex: 1;
  background: #fff;
  border-radius: 5px;
  padding: 5px;
}
